"use client"
import React, { useState } from 'react'
import { Input } from '@heroui/input'
import { ToastContainer, toast } from 'react-toastify'
import { Button } from '@heroui/button'
import { database } from "../firebase";
import { ref, set } from "firebase/database";

const Register = () => {
    const [name, setName] = useState("")
    const [phone, setphone] = useState("")
    const [email, setemail] = useState("")
    const [registration, setregistration] = useState("")
    const [resetCounter, setResetCounter] = useState(0)

    const formSubmit = async () => {
        if (name === "" || email === "" || phone === "" || registration === "") {
            console.log("Please fill all the fields");
            return;
        }

        await set(ref(database, `registration/${email.split("@")[0]}`), {
            Name: name,
            Email: email,
            Phone: phone,
            RegNo: registration,
        })

        console.log("Data submitted successfully");

        // Clear fields
        setName("");
        setemail("");
        setphone("");
        setregistration("");

        // Force rerender for Input fields
        setResetCounter(prev => prev + 1);
    }

    return (
        <div
            key={resetCounter}
            className='flex flex-col border-2 border-gray-300 rounded-lg p-5 gap-y-5'
        >
            <Input
                label="Name"
                placeholder="Enter your name"
                type='text'
                value={name}
                onChange={(e) => setName(e.target.value)} />

            <Input
                label="Phone no."
                placeholder="Enter your phone number"
                type='text'
                value={phone}
                onChange={(e) => setphone(e.target.value)} /> 

            <Input
                label="Email."
                placeholder="Enter your email"
                type='text' 
                value={email}
                onChange={(e) => setemail(e.target.value)} />

            <Input
                label="Registration no."
                placeholder="Enter your registration number"
                type='text' 
                value={registration}
                onChange={(e) => setregistration(e.target.value)} />

            <Button className="w-fit px-3" variant="ghost" color="success" onPress={formSubmit}>
                SUBMIT
            </Button>
        </div>
    )
}

export default Register
